html, body {
  width: 100%;
  height: 100%;
  padding: 0px;

  overflow: hidden;
}

body {
  margin: 0;
}


#root 
{
  width: 100%;
  height: 100%;

  padding: 0;
  margin: 0;

  #navbar {
    position: absolute;
    top: -2.4em;
    padding-top: 0.8em;
    padding-bottom: 1em;
    width: 100%;
    transition: 0.3s;
    background-color: rgba(204, 255, 218, 0.5);

    z-index: 1;

    ul {
      display: flex;
      justify-content: space-around;
      list-style-type: none;
      margin: 0;
    }
    * {
      text-decoration: none;
      color: black;
    }
  }
  #navbar:hover {
    top: 0;
  }

  #content {
    display: block;
    width: 100%;
    height: 100%;

  }
}